import React from 'react';
import './main.css';
import './sprint.css';
import './bootstrap-theme.min.css';
import Card from 'react-bootstrap/Card';
import Table from 'react-bootstrap/Table';
import InfiniteScroll from 'react-infinite-scroll-component';
const urls = require('./urls');

class DateChartComponent extends React.PureComponent  {

	state = {
		data: [],
		count: {prev: 0, next: 50},
		hasMore: true,
		current: []
	};
	
	componentDidMount = async() => {
		this._isMounted = true;
		await this.loadData();
		await this.setState({current: this.state.data.slice(this.state.count.prev, this.state.count.next)});
	}
	
	componentWillUnmount() {
		this._isMounted = false;
	}
	
	loadData = async() => {
		try {
			let response = await fetch(urls.RECENTURL);
			let json = await response.json();		
			for (let i = 0; i < json.length; i++) {
				this.setState(previousState => {
					let data = [...previousState.data];
					data.push({
						player: json[i].player,
						game: json[i].game,
						date: json[i].date,
						link: json[i].link
					})							
					return { data };
				})				
			}
		} catch {
			console.log('oopsie woopsie');
		}
	}
	
	getMoreData = () => {
		if (this.state.current.length >= this.state.data.length) {
			this.setState({hasMore: false});
		    return;
		}
		
		setTimeout(() => {
			this.setState({current: this.state.current.concat(this.state.data.slice(this.state.count.prev + 50, this.state.count.next + 50))});
		}, 100);
		
		this.setState(prevState => ({
			count: {
				...prevState.count,
				prev: prevState.count.prev + 50,
				next: prevState.count.next + 50
			} 
		}))
	}

	render() {	
		const { current, hasMore } = this.state;
		return (
			<div id="app-root">
				<Card style={{ borderWidth: "2px", borderColor: "#4179fc", borderRadius: "20px", margin: "2% 10% 2% 10%", background: "#b2ddff" }} >
					<Card.Body>
						<Card.Title style={{ fontWeight: "bold", fontSize: "24px" }}>Latest Clears</Card.Title>
						<br/>
						<InfiniteScroll 
							dataLength={current.length} 
							next={this.getMoreData} 
							hasMore={hasMore} 
						>
						<Table>
							<tbody>
								<tr>
									<th style={{textAlign: "center", width: "4%"}}>#</th>
									<th style={{textAlign: "center"}}>Player</th>
									<th style={{textAlign: "center"}}>Game</th>
									<th style={{textAlign: "center"}}>Date</th>
									<th style={{textAlign: "center"}}>Video</th>
								</tr>
								{current.map((obj, index) => (
									<tr key={index}>
										<th style={{textAlign: "center"}}>{index+1}</th>
										<th style={{textAlign: "center"}}>{obj.player}</th>
										<th style={{textAlign: "center"}}>{obj.game}</th>
										<th style={{textAlign: "center"}}>{obj.date}</th>
										<th style={{textAlign: "center"}}>
											<a style={{textDecoration: "underline", fontStyle: "italic", color: "green"}} href={obj.link} target="_blank" rel="noreferrer">
													Link
												</a>
											</th>
										</tr>
									))}
								</tbody>
							</Table>
						</InfiniteScroll>
					</Card.Body>
				</Card >
			</div>
		);
	}
};

export default DateChartComponent;